@import "~antd/dist/antd.css";

#root {
  background-color: rgba(210, 210, 210, 0.2);
  height: 100%;
}

.ant-layout-sider-children {
  width: 100%;
}
.sidebarContainer,
.sidebar .ant-layout-sider-trigger {
  min-width: 300px;
}
.ant-drawer-body {
  background-color: #001529;
  padding: 0px;
  width: 300px;
  margin: 0px;
}
/********* Media Queries ********/
@media only screen and (max-width: 600px) {
  .sidebarContainer {
    display: none;
  }
  .showMenu {
    visibility: visible;
  }

  .content {
    padding: 16px;
  }
}

@media only screen and (min-width: 600px) {
  .sidebarContainer {
    display: block;
    height: 100%;
  }
  .sidebar {
    width: 300px;
    height: 100%;
  }
  .sidebarContainer {
    width: 300px;
    background-color: red;
    display: block;
  }
  .showMenu {
    visibility: hidden;
  }
  .content {
    margin: 24px 16px;
    padding: 24px;
  }
}
